import { initializeApp } from "firebase/app";
import { getDatabase, ref, set, get, child } from "firebase/database";

const firebaseConfig = {
  apiKey: "AIzaSyA8vw0soQusug-FIf4xrFe_ESwCBDh0exg",
  authDomain: "the-knight-and-the-nut.firebaseapp.com",
  projectId: "the-knight-and-the-nut",
  storageBucket: "the-knight-and-the-nut.firebasestorage.app",
  messagingSenderId: "430524791611",
  appId: "1:430524791611:web:30279328f66c087d37551e",
  measurementId: "G-6TB408BWNX",
  databaseURL: "https://the-knight-and-the-nut-default-rtdb.firebaseio.com/"
};

const app = initializeApp(firebaseConfig);
const db = getDatabase(app);

export async function writeData(path: string, data: any): Promise<boolean> {
  const dataRef = ref(db, path);
  const snapshot = await get(dataRef);
  if (snapshot.exists()) {
    console.log(` UserId ${path}  tn ti, khng ghi !`);
    return false;
  } else {
    await set(dataRef, data);
    return true;
  }
}

export async function getUser(userId: string): Promise<any | null> {
  try {
    const userRef = ref(db, `users/${userId}`);
    const snapshot = await get(userRef);
    
    if (snapshot.exists()) {
      return snapshot.val();
    } else {
      return null;
    }
  } catch (error) {
    throw error;
  }
}

export function updateData(path: string, data: any): void {
  const dataRef = ref(db, path);
  set(dataRef, data)
    .then(() => console.log(" D liu  c ghi thnh cng"))
    .catch((error) => console.error(" Ghi d liu tht bi:", error));
}

export async function getAllData(path: string): Promise<any> {
  const snapshot = await get(child(ref(db), path));
  if (snapshot.exists()) {
    return snapshot.val();
  } else {
    console.warn(" Khng c d liu ti path:", path);
    return null;
  }
}

(window as any).FirebaseBundle = {
  writeData,
  updateData,
  getAllData,
  getUser
};
